/**
 * WaveSky molecule 컴포넌트
 *
 * @author RWB
 * @since 2025.06.11 Wed 14:16:52
 */

import { getHour } from '@kapoo/common';
import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import Sky from '../../atom/Sky';
import Wave from '../../atom/Wave';

import type { DetailedHTMLProps, HTMLAttributes } from 'react';

export interface WaveSkyProps extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>
{
	/**
	 * 시간대
	 */
	readonly hour?: Hour;
}

/**
 * WaveSky molecule 컴포넌트 반환 메서드
 *
 * @param {WaveSkyProps} param0: WaveSkyProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function WaveSky({ hour, className, ...props }: WaveSkyProps): React.JSX.Element
{
	const targetHour = hour ?? getHour();

	return (
		<div
			className={cn('size-full', className)}
			data-component="WaveSky"
			data-time={targetHour}
			{...props}
		>
			<div
				className={cn('w-full', targetHour, {
					'fill-[#00000C]': targetHour === 0 || targetHour === 23,
					'fill-[#010A10]': targetHour === 21,
					'fill-[#020111]': targetHour >= 1 && targetHour <= 3,
					'fill-[#071B26]': targetHour === 20,
					'fill-[#090401]': targetHour === 22,
					'fill-[#154277]': targetHour === 18,
					'fill-[#163C52]': targetHour === 19,
					'fill-[#1E528E]': targetHour >= 16 && targetHour <= 17,
					'fill-[#2473AB]': targetHour === 15,
					'fill-[#2D91C2]': targetHour === 14,
					'fill-[#303340]': targetHour === 4,
					'fill-[#4A4969]': targetHour === 6,
					'fill-[#505E75]': targetHour === 5,
					'fill-[#57C1EB]': targetHour === 13,
					'fill-[#6D8BB9]': targetHour === 7,
					'fill-[#82ADDB]': targetHour === 8,
					'fill-[#90DFFE]': targetHour === 12,
					'fill-[#94C5F8]': targetHour === 9,
					'fill-[#9BE2FE]': targetHour === 11,
					'fill-[#b7EAFF]': targetHour === 10
				})}
			>
				<Wave fillColor="inherit" height={30} width="100%" />
			</div>

			<Sky className="size-full" hour={targetHour} />
		</div>
	);
}