'use client';

import { getHour } from '@kapoo/common';
import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import styles from './Sky.module.scss';

import type { DetailedHTMLProps, HTMLAttributes } from 'react';

export interface SkyProps extends DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>
{
	/**
	 * 시간대
	 */
	readonly hour?: Hour;
}

/**
 * 하늘 atom 컴포넌트 반환 메서드
 *
 * @param {SkyProps} param0: SkyProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function Sky({ hour, className, ...props }: SkyProps): React.JSX.Element
{
	const time = hour ?? getHour();

	const hourStyle = Array.from({ length: 24 })
		.map((i, j) => ({ [styles[`hour-${j}`]]: time === j }));

	return (
		<div
			className={cn('size-full transition duration-300', styles.sky, hourStyle, className)}
			data-component="Sky"
			{...props}
		/>
	);
}